a {
  text-decoration: none;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Open Sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
}

.page__body {
  min-width: 320px;
  min-height: 100%;
  display: grid;
  grid-template: min-content 1fr min-content/auto;
  margin: 0;
  font-family: "Open Sans", "Arial", sans-serif;
}

.page__modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 2;
}

.page-header {
  border-top: 1px solid #ececec;
  overflow-wrap: break-word;
}
@media (min-width: 768px) {
  .page-header {
    border: none;
  }
}

@media (max-width: 767px) {
  .page-header__wrapper {
    border-bottom: 1px solid #ececec;
  }
}
@media (min-width: 768px) {
  .page-header__wrapper {
    display: grid;
    grid-template: minmax(88px, auto) auto/1fr 334px 334px 1fr;
  }
}
@media (min-width: 1150px) {
  .page-header__wrapper {
    grid-template: minmax(88px, auto) auto/1fr 450px 149px 451px 1fr;
  }
}

.page-header__main-navigation--hot {
  display: none;
}
@media (min-width: 768px) {
  .page-header__main-navigation--basic {
    grid-row: 2/3;
    grid-column: 2/3;
    font-size: 15px;
    z-index: 2;
  }
  .page-header__main-navigation--search {
    grid-row: 1/2;
    grid-column: 2/4;
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1150px) {
  .page-header__main-navigation--basic {
    grid-row: 1/2;
  }
  .page-header__main-navigation--search {
    grid-column: 4/5;
    justify-content: flex-end;
  }
  .page-header__main-navigation--hot {
    display: block;
    grid-row: 2/3;
    grid-column: 2/3;
    z-index: 2;
  }
}

.page-header__box {
  padding: 17px 3px 18px 0;
  display: block;
  width: min-content;
  margin: 0 auto;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-header__box {
    grid-row: 1/2;
    grid-column: 2/3;
    margin: 0 0 0 -7px;
    padding: 17px 0 15px;
  }
}
@media (min-width: 1150px) {
  .page-header__box {
    grid-column: 3/4;
    justify-self: center;
    margin: 0;
    padding: 5px 0 3px;
  }
}

.page-header__holder {
  position: relative;
  width: 260px;
  height: 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .page-header__holder {
    display: none;
  }
}

.page-header__toggle {
  position: absolute;
  right: -14px;
  top: -65px;
  width: 50px;
  height: 50px;
  border: none;
  background: transparent;
}
.page-header__toggle::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 21px;
  height: 20px;
  background-image: url("../img/icon_menu_close.svg");
  background-repeat: no-repeat;
  transform: translate(-50%, -50%);
}
.page-header__toggle:hover {
  cursor: pointer;
}
.page-header__toggle--nojs {
  display: none;
}
.page-header__toggle--closed::before {
  width: 20px;
  height: 2px;
  background-image: none;
  background-color: #000000;
  box-shadow: 0 -6px 0 0 #000000, 0 6px 0 0 #000000;
}

.page-header__shipping {
  display: none;
  margin: 0;
  padding-top: 20px;
  color: #444444;
  font-size: 15px;
  text-align: right;
}
@media (min-width: 768px) {
  .page-header__shipping {
    display: block;
    grid-row: 2/3;
    grid-column: 3/4;
    padding-top: 22px;
    z-index: 2;
  }
}
@media (min-width: 1150px) {
  .page-header__shipping {
    grid-column: 4/5;
  }
}

@media (min-width: 768px) {
  .page-header__paint {
    grid-row: 2/3;
    grid-column: 1/5;
    background-color: #f9f9f9;
    background-image: url("../img/header_paint.jpg");
    background-position: 50% 100%;
    background-repeat: no-repeat;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    z-index: 0;
  }
  .page-header__paint--inner {
    background-color: #f1f1f1;
    background-image: none;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-header__paint {
    background-image: url("../img/header_paint@2x.jpg");
  }
}
@media (min-width: 768px) {
  .page-header__paint {
    grid-column: 1/6;
  }
}

.logo {
  display: block;
  padding: 4px;
}
.logo--inner:hover .logo__image {
  opacity: 0.6;
}
.logo--inner:active .logo__image {
  opacity: 0.3;
}
.logo--bottom {
  color: #000000;
}
.logo--bottom:hover {
  color: #46c1ae;
}
.logo--bottom:active {
  opacity: 0.3;
}

.logo__image {
  display: block;
  width: 86px;
  height: 35px;
  object-fit: contain;
}
@media (min-width: 768px) {
  .logo__image--header {
    width: 115px;
    height: 48px;
  }
  .logo__image--footer {
    width: 103px;
    height: 36px;
  }
}
@media (min-width: 1150px) {
  .logo__image--header {
    width: 149px;
    height: 72px;
  }
}

.main-navigation {
  font-size: 17px;
  line-height: 24px;
  font-weight: bold;
}
.main-navigation--nojs .main-navigation__toggle {
  display: none;
}
@media (max-width: 767px) {
  .main-navigation {
    transition: 200ms height;
    overflow: hidden;
  }
  .main-navigation--closed {
    display: none;
  }
  .main-navigation--search .main-navigation__item:last-child {
    border: none;
  }
  .main-navigation--ease-in {
    transition-timing-function: ease-in;
  }
  .main-navigation--ease-out {
    transition-timing-function: ease-out;
  }
}
@media (min-width: 768px) {
  .main-navigation--basic {
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .main-navigation--basic {
    font-size: 17px;
  }
}

.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .main-navigation__list--basic {
    display: flex;
    flex-wrap: wrap;
  }
}
@media (min-width: 1150px) {
  .main-navigation__list--hot {
    display: flex;
    flex-wrap: wrap;
  }
}

.main-navigation__item {
  border-bottom: 1px solid #ececec;
}
.main-navigation__item--basic:first-child {
  border-top: 1px solid #ececec;
}
@media (min-width: 768px) {
  .main-navigation__item--basic {
    width: 142px;
    border: none;
  }
  .main-navigation__item--basic:first-child {
    border-top: none;
  }
  .main-navigation__item--basic:nth-child(odd) {
    margin-right: 26px;
  }
  .main-navigation__item--search {
    border-bottom: none;
  }
  .main-navigation__item--lens {
    padding: 0 0 0 48%;
    line-height: 18px;
  }
  .main-navigation__item--cart {
    padding: 8px 0 6px 5px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
}
@media (min-width: 1150px) {
  .main-navigation__item--basic:nth-child(odd) {
    width: 185px;
    margin-right: 0;
    padding-right: 10px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-navigation__item--basic:nth-child(even) {
    width: 220px;
    padding-left: 35px;
  }
  .main-navigation__item--additional {
    width: 175px;
    padding: 20px 0 24px 1px;
    font-size: 15px;
    line-height: 24px;
    border: none;
  }
  .main-navigation__item--additional:nth-child(odd) {
    margin-right: 45px;
  }
  .main-navigation__item--lens {
    padding: 0;
  }
  .main-navigation__item--cart {
    padding: 8px 2px 6px 6px;
  }
}

.main-navigation__wrapper {
  margin: 0 auto;
  padding: 28px 30px 26px 46px;
  width: 260px;
}
.main-navigation__wrapper--lens {
  padding-left: 2px;
}
.main-navigation__wrapper--cart {
  padding-left: 0;
}
@media (min-width: 768px) {
  .main-navigation__wrapper {
    width: auto;
    margin: 0;
    padding-left: 0;
  }
  .main-navigation__wrapper--basic {
    padding: 22px 0;
  }
  .main-navigation__wrapper--search {
    height: 100%;
    padding: 0;
  }
}
@media (min-width: 1150px) {
  .main-navigation__wrapper--basic {
    padding: 34px 0 30px;
  }
}

.main-navigation__link {
  color: #000000;
}
.main-navigation__link:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-navigation__link:hover .main-navigation__icon {
  fill: #63d1bb;
  opacity: 1;
}
.main-navigation__link:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-navigation__link:active .main-navigation__icon {
  opacity: 0.3;
}
.main-navigation__link--with-icon {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .main-navigation__link--search {
    justify-content: center;
    width: 83px;
    height: 100%;
    padding: 36px 0 34px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
  }
  .main-navigation__link--cart {
    justify-content: flex-end;
    width: 258px;
    padding: 25px 0 25px 25px;
    text-align: right;
  }
}

.main-navigation__icon {
  opacity: 0.2;
}
.main-navigation__icon--search {
  margin-right: 26px;
}
.main-navigation__icon--cart {
  height: 20px;
  margin-right: 25px;
  padding-bottom: 2px;
}
@media (min-width: 768px) {
  .main-navigation__icon--search {
    margin-right: 0;
  }
  .main-navigation__icon--cart {
    margin-right: 30px;
  }
}

.main-navigation__text {
  max-width: 182px;
}
@media (min-width: 768px) {
  .main-navigation__text--search {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-main {
  overflow-wrap: break-word;
}
.page-main--catalog {
  padding-bottom: 58px;
}
@media (min-width: 768px) {
  .page-main--catalog {
    padding-bottom: 64px;
  }
}
@media (min-width: 1150px) {
  .page-main--catalog {
    padding-bottom: 105px;
  }
}

.page-main__wrap {
  display: grid;
  grid-template: auto 14px 10px/auto;
}
@media (min-width: 1150px) {
  .page-main__wrap {
    grid-template: auto 19px 15px/auto;
  }
}

.page-main__holder--index {
  grid-row: 1/3;
  grid-column: 1/2;
  align-self: start;
  display: grid;
  grid-template: auto 120px 206px auto/1fr 260px 1fr;
  background-color: #f9f9f9;
  overflow-x: hidden;
}
.page-main__holder--inner {
  padding: 73px 0 74px;
  background-color: #f1f1f1;
}
@media (max-width: 767px) {
  .page-main__holder--inner {
    margin-bottom: 16px;
  }
}
@media (min-width: 768px) {
  .page-main__holder--index {
    grid-template: auto 371px 119px auto/1fr 668px 1fr;
  }
  .page-main__holder--inner {
    padding: 33px 0 63px;
  }
}
@media (min-width: 1150px) {
  .page-main__holder--index {
    grid-template: auto 355px 119px auto/1fr 1050px 1fr;
  }
  .page-main__holder--inner {
    padding: 34px 0 79px;
  }
}

.page-main__box {
  grid-row: 2/4;
  grid-column: 1/4;
  justify-self: center;
  align-self: end;
  display: flex;
  align-items: flex-end;
  padding-bottom: 128px;
  padding-left: 1px;
  z-index: 0;
}
@media (min-width: 768px) {
  .page-main__box {
    display: block;
    padding: 0;
  }
}

.page-main__background {
  width: 575px;
  height: 355px;
  margin: 0 auto;
  object-fit: contain;
}
@media (min-width: 768px) {
  .page-main__background {
    display: block;
    width: 1150px;
    height: 679px;
  }
}

.page-main__title {
  width: 260px;
  margin: 0 auto;
  font-size: 27px;
  line-height: 30px;
  font-weight: bold;
  text-align: center;
}
.page-main__title--index {
  padding-top: 72px;
  grid-row: 1/2;
  grid-column: 2/3;
  z-index: 1;
}
@media (min-width: 768px) {
  .page-main__title {
    width: 668px;
    font-size: 43px;
    line-height: 50px;
  }
  .page-main__title--index {
    padding-top: 88px;
  }
}
@media (min-width: 1150px) {
  .page-main__title {
    width: 700px;
    font-size: 53px;
    line-height: 57px;
  }
  .page-main__title--index {
    padding-top: 90px;
  }
  .page-main__title--inner {
    padding-left: 3px;
  }
}

@media (min-width: 1150px) {
  .page-main__space {
    display: none;
  }
}

.page-main__sub-navigation {
  grid-row: 3/5;
  grid-column: 1/4;
  z-index: 2;
}
@media (min-width: 768px) {
  .page-main__sub-navigation {
    grid-column: 2/3;
  }
}
@media (min-width: 768px) {
  .page-main__sub-navigation {
    align-self: end;
  }
}

.page-main__waves {
  height: 24px;
  background-image: url("../img/wave_big.svg");
  background-position: 50%;
  background-repeat: repeat-x;
}
.page-main__waves--index {
  grid-row: 2/4;
  grid-column: 1/2;
  align-self: end;
  z-index: 3;
}
.page-main__waves--inner, .page-main__waves--catalogue {
  background-color: #f1f1f1;
}
.page-main__waves--inner {
  margin-bottom: 54px;
}
.page-main__waves--catalogue {
  margin-bottom: 67px;
}
@media (max-width: 767px) {
  .page-main__waves {
    display: none;
  }
}
@media (min-width: 1150px) {
  .page-main__waves {
    height: 34px;
  }
  .page-main__waves--index {
    margin-left: 1px;
    background-image: url("../img/wave_big_desktop.svg");
  }
  .page-main__waves--inner, .page-main__waves--catalogue {
    background-image: url("../img/wave_big_desktop_inner.svg");
  }
  .page-main__waves--inner {
    margin-bottom: 99px;
  }
  .page-main__waves--catalogue {
    margin-bottom: 111px;
  }
}

.page-main__featured,
.page-main__reviews {
  position: relative;
}

@media (min-width: 1150px) {
  .page-main__featured,
.page-main__advantages,
.page-main__reviews,
.page-main__contacts {
    margin: 0 auto;
  }
}

.page-main__lead {
  margin: 0 auto 14px;
  width: 260px;
  font-size: 13px;
  line-height: 24px;
  color: #444444;
}
@media (min-width: 768px) {
  .page-main__lead {
    width: 668px;
    margin: 0 auto 13px;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .page-main__lead {
    width: 700px;
  }
}

.page-main__goods {
  margin-bottom: 30px;
}
@media (min-width: 768px) {
  .page-main__goods {
    margin-bottom: 53px;
  }
}
@media (min-width: 1150px) {
  .page-main__goods {
    margin-bottom: 57px;
  }
}

.page-main__process {
  margin: 0 auto;
}

.sub-navigation {
  font-size: 19px;
  line-height: 24px;
  font-weight: bold;
}
@media (min-width: 768px) {
  .sub-navigation {
    font-size: 20px;
    line-height: 30px;
  }
}

.sub-navigation__list {
  min-height: 119px;
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .sub-navigation__list {
    display: flex;
    flex-wrap: wrap;
  }
}

.sub-navigation__item:hover > .sub-navigation__link::before {
  content: "";
  position: absolute;
  top: -5px;
  bottom: 100%;
  left: 0;
  right: 0;
}
.sub-navigation__item:hover .sub-navigation__wrapper::after {
  opacity: 0.7;
}
.sub-navigation__item:active .sub-navigation__wrapper::after {
  opacity: 0.3;
}
.sub-navigation__item:nth-child(odd) {
  background-color: #63d1bb;
}
.sub-navigation__item:nth-child(odd):hover {
  background-color: #59d5bc;
}
.sub-navigation__item:nth-child(odd):hover > .sub-navigation__link::before {
  background-color: #59d5bc;
}
.sub-navigation__item:nth-child(even) {
  background-color: #56c3b2;
}
.sub-navigation__item:nth-child(even):hover {
  background-color: #46c1ae;
}
.sub-navigation__item:nth-child(even):hover > .sub-navigation__link::before {
  background-color: #46c1ae;
}
@media (min-width: 768px) {
  .sub-navigation__item {
    width: 50%;
  }
}

.sub-navigation__link {
  position: relative;
  display: block;
  height: 100%;
  padding: 24px 0 31px;
  color: #ffffff;
  overflow-wrap: break-word;
}
.sub-navigation__link:hover {
  padding: 19px 0 36px;
}
@media (min-width: 768px) {
  .sub-navigation__link {
    padding: 26px 115px 35px 36px;
  }
  .sub-navigation__link:hover {
    padding: 21px 115px 40px 36px;
  }
}

.sub-navigation__wrapper {
  position: relative;
  margin: 0 auto;
  width: 260px;
  padding-right: 50px;
}
.sub-navigation__wrapper::after {
  content: "";
  position: absolute;
  background-size: cover;
  opacity: 0.5;
}
.sub-navigation__wrapper--decor::after {
  top: -1px;
  right: 6px;
  width: 40px;
  height: 62px;
  background-image: url("../img/icon_table_lamp.svg");
}
.sub-navigation__wrapper--toys::after {
  top: -3px;
  right: -3px;
  width: 60px;
  height: 60px;
  background-image: url("../img/icon_constructor_block.svg");
}
@media (min-width: 768px) {
  .sub-navigation__wrapper {
    width: auto;
    padding-right: 0;
  }
  .sub-navigation__wrapper--decor::after {
    top: 3px;
    right: -80px;
    width: 43px;
    height: 64px;
    background-image: url("../img/icon_table_lamp_tablet_integer.svg");
  }
  .sub-navigation__wrapper--toys::after {
    top: 5px;
    right: -82px;
    width: 61px;
    height: 61px;
  }
}
@media (min-width: 1150px) {
  .sub-navigation__wrapper--toys::after {
    top: 5px;
    right: -80px;
    width: 60px;
  }
}

.featured {
  padding: 21px 0 42px;
}
.featured::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 6px;
  background-image: url("../img/section_border_mobile.svg");
  background-position: 50%;
  background-repeat: repeat;
}
@media (min-width: 768px) {
  .featured {
    padding: 40px 0 69px;
  }
  .featured::after {
    height: 17px;
    background-image: url("../img/section_border_tablet.svg");
  }
}
@media (min-width: 1150px) {
  .featured {
    width: 1050px;
    padding: 54px 0 96px;
  }
  .featured::after {
    height: 23px;
    background-image: url("../img/section_border_desktop.svg");
    background-position: 0;
    background-repeat: no-repeat;
  }
}

.featued__wrapper {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  width: 260px;
}
@media (min-width: 768px) {
  .featued__wrapper {
    display: grid;
    grid-template: auto auto auto auto auto/2fr 1fr 1fr;
    width: 668px;
  }
}
@media (min-width: 1150px) {
  .featued__wrapper {
    grid-template: auto auto auto auto auto/1fr 2fr 1fr 2fr;
    width: 100%;
  }
}

.featured__title {
  margin: 0 0 17px;
  color: #63d1bb;
  font-size: 17px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .featured__title {
    grid-row: 1/2;
    grid-column: 3/4;
    justify-self: end;
    width: 113px;
    padding: 6px 13px 0 0;
    margin-bottom: 48px;
    line-height: 17px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .featured__title {
    grid-row: 1/6;
    grid-column: 1/2;
    width: 132px;
    margin-right: 43px;
    padding: 69px 0 0 32px;
  }
}

.featured__subtitle {
  order: -1;
